import ProductionModel from '../models/production';

export default async function updateProduction(productionList) {
  const Production = ProductionModel();
  await Promise.all(
    productionList.map(async (production) => {
      return Production.findByIdAndUpdate(
        { _id: production._id },
        production.update
      );
    })
  );

  return { message: 'Production updated successfully' };
}
